import { G, Image } from "react-native-svg";

const FISH_SIZE = 50;
const FISH_LEFT = 200;

export default function Fish(props) {
  const { gameOver, fish, posY, ghost, y, graphics } = props;
  const angle = -y;
  const opacity = ghost ? Math.abs(Math.sin(ghost / 100)) : 1;
  return (
    <>
      <G y={posY} x={FISH_LEFT}>
        <G
          transform={
            graphics > 1 ? `rotate(${Math.min(35, angle / 2)})` : undefined
          }
        >
          <G x={-FISH_SIZE / 2} y={-FISH_SIZE / 2}>
            {ghost > 0 && (
              <Image width={FISH_SIZE} height={FISH_SIZE} href={fish.ghost} />
            )}
            <Image
              width={FISH_SIZE}
              height={FISH_SIZE}
              href={gameOver ? fish.ghost : fish.image}
              opacity={opacity}
            />
          </G>
        </G>
      </G>
    </>
  );
}
